.detail {
  display: flex;
  flex-direction: column;

  &__title {}

  &__contentblock {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    gap: 25px;

    @include small-desktop {
      flex-direction: column;
      align-items: center;
    }
    @include mobile {
      gap: 12px;
    }

    &--revers {
      flex-direction: row-reverse;

      @include small-desktop {
        flex-direction: column;
        align-items: center;
      }
    }
  }
  &__img {
    display: block;
    border-radius: 20px;
    width: calc((100% - 25px)/2);
    max-height: 450px;
    overflow: hidden;

    @include small-desktop {
      width: auto;
      max-width: 100%;
    }

    & img {
      max-width: 100%;
      object-fit: cover;

      @include tablet {
        width: 500px;
      }
      @include mobile {
        width: 320px;
      }
    }

  }
  &__list {
    margin: 0;
    padding-inline-start: 20px;
    width: calc((100% - 25px)/2);
    font-weight: 400;
    font-size: 16px;
    line-height: 1.2;
    color: var(--dark-text-color);

    @include small-desktop {
      width: 100%;
    }
  }

  &__item {
    list-style: disc;
    font-weight: inherit;
    font-size: inherit;
    line-height: inherit;
    color: inherit;

    &::marker {
      width: 6px;
      height: 6px;
      color: var(--blue-color);
    }
    &:not(:last-child) {
      margin-bottom: 10px;

      @include tablet{
        margin-bottom: 4px;
      }
    }
  }
}
