
.equipment-card {
  display: flex;
  flex-direction: column;
  gap: 8px;
  // justify-content: space-between;
  padding: 40px;
  width: 100%;
  background-color: var(--light-color);
  transition: box-shadow .4s ease-in-out;

  &:focus {
    outline: none;
    box-shadow: 8px 8px 4px 0 rgba(168, 168, 168, .25);
  }
  @include hover {
    box-shadow: 8px 8px 4px 0 rgba(168, 168, 168, .25);
  }

  @include small-desktop {
    padding: 40px 20px;
  }

  &__inner {
    display: flex;
    justify-content: space-between;

    @include small-tablet {
      flex-direction: column-reverse;
      align-items: center;
      gap: 12px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 20px;

    @include mobile {
      gap: 12px;
    }
  }

  &__title {
    margin: 0;
    font-family: var(--title-family);
    font-weight: 700;
    font-size: 26px;
    line-height: 110%;
    color: var(--blue-color);

    @include tablet {
      font-size: 24px;
    }
    @include mobile {
      font-size: 20px;
      text-align: center;
    }

    & a {
      font-weight: inherit;
      font-size: inherit;
      line-height: inherit;
      color: inherit;
      transition: color .4s ease-in-out;

      @include hover {
        color: var(--orange-color);
      }
    }
  }

  &__img {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0;
    max-width: 420px;

    &:focus {
      & img {
      transform: scale(1.1);
      }
    }
    @include hover {
      & img {
      transform: scale(1.1);
      }
    }

    @include small-desktop {
      max-width: 370px;
    }
    @include tablet {
      max-width: 340px;
    }
    @include mobile {
      max-width: 100%;
    }

    & img {
      width: 420px;
      max-width: 100%;
      object-fit: cover;
      filter: drop-shadow(-5px 5px 4px rgba(0, 0, 0, 0.2));
      transition: transform .4s ease-in-out;

      @include small-desktop {
        width: 370px;
      }
      @include tablet {
        width: 340px;
      }
      @include mobile {
        width: 100%;
        max-width: 100%;
      }
    }
  }

  &__desr {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: var(--dark-text-color);
    -webkit-box-orient: vertical;
    display: -webkit-box;
    overflow: hidden !important;
    text-overflow: ellipsis;
    -webkit-line-clamp: 5;

    @include tablet {
      font-size: 14px;
      line-height: 120%;
      -webkit-line-clamp: 4;
    }
    @include mobile {
      text-align: center;
    }
  }

  &__action {
    display: flex;
    align-items: center;
    gap: 20px;

    @include mobile {
      justify-content: center;
    }
  }

  &__link {
    position: relative;
    z-index: 1;
    margin-top: auto;
    width: calc(100%/2 - 10px);
    max-width: 200px;
    min-width: 150px;
    background-color: transparent;
    transition: filter 0.4s ease-in-out, background-color .4s ease-in-out;

    // @include tablet {
    //   max-width: 160px;
    // }

    @include mobile {
      align-self: center;
      // max-width: 100%;
      max-width: 180px;
      min-width: auto;
      width: 100%;
    }

    &::after {
      content: '';
      position: absolute;
      z-index: -1;
      inset: 0;
      border-radius: inherit;
      width: 100%;
      height: 100%;
      background: linear-gradient(172deg, #469bff 0%, #153059 100%);
      opacity: 1;
      visibility: visible;
      transition: opacity .4s ease-in-out, visibility .4s ease-in-out;
    }

    &:focus {
      outline: none;
      background-color: var(--orange-color);
      filter: brightness(1.1) drop-shadow(0 0 5px rgba(0, 0, 0, .5));

      &::after {
        opacity: 0;
        visibility: hidden;
      }
    }

    @include hover {
      background-color: var(--orange-color);
      filter: brightness(1.1) drop-shadow(0 0 5px rgba(0, 0, 0, .5));

      &::after {
        opacity: 0;
        visibility: hidden;
      }
    }

    &:active {
      background-color: var(--orange-color);
      filter: brightness(1.1) drop-shadow(0 0 5px rgba(0, 0, 0, .5));

      &::after {
        opacity: 0;
        visibility: hidden;
      }
    }
  }

  &__btn {
    position: relative;
    z-index: 1;
    margin-top: auto;
    width: calc(100%/2 - 10px);
    max-width: 200px;
    min-width: 150px;
    // background-color: transparent;
    transition: filter 0.4s ease-in-out, background-color .4s ease-in-out;
  }
}
