.section-pdf {

  &__title {
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    margin-bottom: 20px;
  }

  &__item {
    width: calc(100%/4 - 20px*3/4);

    @include small-tablet {
      width: calc(100%/2 - 20px*1/2);
    }

    @include mobile {
      width: 100%;
    }
  }

  &__link {}

  &__note {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.2;
    color: var(--dark-text-color);

    @include mobile {
      font-size: 14px;
    }
  }
}

.link-pdf {
  display: flex;
  align-items: center;
  gap: 6px;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.2;
  color: var(--dark-text-color);
  transition: color .3s ease-in-out;

  @include hover {
    color: var(--orange-color);
  }

  @include mobile {
    font-size: 14px;
  }

  & img {
    width: 40px;
    height: 40px;

    @include mobile {
      width: 30px;
      height: 30px;
    }
  }

  & span {
    margin-top: 3px;
    font-weight: inherit;
    font-size: inherit;
    line-height: inherit;
    color: currentColor;
  }
}
