.feature {

  &__title {}

  &__content {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0;
  }

  &__inner {
    position: relative;
    z-index: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0;
  }

  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    font-size: 0;
  }

  &__hotpoint {}


}
.hotpoint {
  position: absolute;
  display: flex;
  flex-direction: column;

  &__btn {
    border: none;
    border-radius: 100%;
    width: 20px;
    height: 20px;
    font-size: 0;
    outline: 2px solid var(--light-color);
    outline-offset: -5px;
    background-color: var(--orange-color);
    cursor: pointer;
    transition: transform .4s ease-in-out;

    &:focus, &:active {
      outline: 2px solid var(--light-color);
      transform: scale(1.1);
    }
  }

  &__content {
    position: absolute;
    z-index: 10;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    border: none;
    border-radius: 10px;
    padding: 10px;
    width: max-content;
    max-width: 260px;
    background-color: var(--blue-color);
    opacity: 0;
    visibility: hidden;
    transition: opacity .4s ease-in-out, visibility .4s ease-in-out;

    .hotpoint__btn:focus ~ &,
    .hotpoint__btn.is-open ~ &{
      opacity: 1;
      visibility: visible;
    }

    @include mobile {
      max-width: 200px;
    }

    &--left {
      top: 50%;
      left: calc(20px + 15px);
      transform: translateY(-50%);
    }
    &--right {
      top: 50%;
      right: calc(20px + 15px);
      transform: translateY(-50%);
    }
    &--top {
      top: -10px;
      left: 50%;
      transform: translate(-50%, -100%);
    }
    &--bottom {
      bottom: -10px;
      left: 50%;
      transform: translate(-50%, 100%);
    }
  }
  &__img {
    font-size: 0;
    & img {
      width: 100%;
    }
  }
  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    text-align: center;
    color: var(--light-color);
  }
  &__arrow {
    position: absolute;
    display: inline-block;
    border: 8px solid transparent;
    width: 16px;
    height: 16px;

    .hotpoint__content--left & {
      top: 50%;
      left: -15px;
      transform: translateY(-50%);
      border-right: 8px solid var(--blue-color);
    }
    .hotpoint__content--right & {
      top: 50%;
      right: -15px;
      transform: translateY(-50%);
      border-left: 8px solid var(--blue-color);
    }
    .hotpoint__content--top & {
      bottom: -15px;
      left: 50%;
      transform: translateX(-50%);
      border-top: 8px solid var(--blue-color);
    }
    .hotpoint__content--bottom & {
      top: -15px;
      left: 50%;
      transform: translateX(-50%);
      border-bottom: 8px solid var(--blue-color);
    }
  }
}
