.video-post {
  position: relative;
  border-radius: 20px;
  width: 100%;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  background-color: var(--dark-text-color);

  &__btn-close {
    position: absolute;
    z-index: 3;
    top: 20px;
    right: 20px;
    border-radius: 100%;
    width: 36px;
    height: 36px;
    color: var(--light-color);

    &::before {
      content: '';
      position: absolute;
      inset: 0;
      border-radius: inherit;
      width: 100%;
      height: 100%;
      background-color: var(--dark-text-color);
      opacity: 0.5;
      transition: background-color .3s ease-in-out, opacity .3s ease-in-out;
    }

    & svg {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 16px;
      height: 16px;
      fill: var(--light-color);
      transform: translate(-50%, -50%);
    }

  @include hover {
    &::before {
      opacity: 1;
      background-color: var(--orange-color);
    }
  }
  }

  &__img {
    position: absolute;
    inset: 0;
    display: block;
    border-radius: inherit;
    width: 100%;
    height: 100%;
    overflow: hidden;

    & img {
      border-radius: inherit;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__youtube {
    position: absolute;
    inset: 0;
    display: block;
    border-radius: inherit;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  &__iframe {
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    border: none;
    -o-object-fit: cover;
       object-fit: cover;
  }
}
