.page-card-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  padding: 20px;
  height: 100%;
  background-color: var(--light-color);
  transition: box-shadow .4s ease-in-out;

  @include mobile {
    gap: 16px;
    padding: 16px;
  }

  &:focus {
    outline: none;
    box-shadow: inset 0 4px 4px 0 rgba(248, 252, 255, .25), 0 4px 10px 0 rgba(104, 104, 104, .25);
  }

  @include hover {
    box-shadow: inset 0 4px 4px 0 rgba(248, 252, 255, .25), 0 4px 10px 0 rgba(104, 104, 104, .25);
  }

  &:active{
    box-shadow: inset 0 4px 4px 0 rgba(248, 252, 255, .25), 0 4px 10px 0 rgba(104, 104, 104, .25);
  }

  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-height: 100px;

    & img {
      max-width: 100%;
      height: 100px;
      object-fit: cover;
    }
  }

  &__title {
    margin: 0;
    font-family: var(--title-family);
    font-weight: 600;
    font-size: 20px;
    line-height: 1;
    text-align: center;
    color: var(--dark-text-color);


    @include small-tablet {
      font-size: 18px;
    }
  }
}
