.machine {

  &__title {
    max-width: 960px;
    font-family: var(--title-family);
    font-weight: 700;
    font-size: 36px;
    line-height: 1;
    letter-spacing: 1px;
    color: var(--blue-color);

    @include small-desktop {
      max-width: 800px;
      font-size: 28px;
    }

    @include mobile {
      font-size: 24px;
    }
  }

  &__inner {
    display: flow-root;

    &::after {
      content: '';
      clear: both;
    }
  }
  &__short-descr {
    margin-bottom: 24px;
  }
  &__btn {
    margin-bottom: 24px;
    min-width: 200px;
  }

  &__infomation {

    &.textredactor {
      margin-bottom: 0;
    }

    @include small-desktop {
      width: 100%;
    }
  }
// начало на удаление после обновления каталога
  .machine__features {
    margin-top: 0;
    margin-bottom: 16px;
    -webkit-padding-start: 20px;
    padding-inline-start: 20px;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.2;
    color: var(--dark-text-color);
    @include mobile {
      margin-bottom: 8px;
    }
  }
  .machine__feature {
    list-style: disc;
    font-weight: inherit;
    font-size: inherit;
    line-height: inherit;
    color: inherit;
    &::marker {
      width: 6px;
      height: 6px;
      color: var(--blue-color);
    }
    &:not(:last-child) {
      margin-bottom: 4px;
    }
  }
  .machine__description {
    margin-bottom: 24px;
    p {
      margin: 0;
      font-weight: 400;
      font-size: 16px;
      line-height: 1.2;
      color: var(--dark-text-color);
      &:not(:last-child) {
        margin-bottom: 16px;

        @include mobile {
          margin-bottom: 8px;
        }
      }
    }
  }
// конец на удаление после обновления каталога

  &__text {
    margin: 32px 0;

    @include mobile {
      margin: 16px 0;
    }

    & p {
      margin: 0;
      font-weight: 400;
      font-size: 16px;
      line-height: 1.2;
      color: var(--dark-text-color);

      @include mobile {
        font-size: 14px;
      }

      &:not(:last-child) {
        margin-bottom: 16px;

        @include mobile {
          margin-bottom: 8px;
        }
      }
    }
  }

  &__pdf-link {
    display: flex;
    align-items: center;
    gap: 6px;

    & img {
      width: 20px;
      height: 20px;
    }
  }

  &__gallery {
    position: relative;
    padding: 0 50px;

    @include mobile {
      padding: 0 20px;
    }
  }

  &__thumbnail {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 20px;
    margin-bottom: 20px;
    width: 50%;
    max-width: 100%;
    max-height: 400px;
    float: right;
    // overflow: hidden;

    @include small-desktop {
      margin-left: 0;
      width: 100%;
    }

  }
}

.machine-slider {
  margin-left: 20px;
  margin-bottom: 20px;
  width: 50%;
  float: right;

  @include small-desktop {
    margin-left: 0;
    width: 100%;
  }

  & .swiper-slide {
    display: flex;
  }

  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 100%;
    // max-height: 400px;
    overflow: hidden;

    & img {
      width: 100%;
      // height: 100%;
      height: auto;
      max-height: 400px;
      max-width: 100%;
    }
  }

  &__pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 40px;

    @include mobile {
      margin-top: 24px;
    }

    & .swiper-pagination-bullet {
      display: inline-block;
      margin: 0 4px;
      border-radius: 2px;
      width: 54px;
      height: 8px;
      background-color: var(--blue-light-color);
      cursor: pointer;

      @include mobile {
        width: 24px;
        height: 4px;
      }
    }
  }
}

.machine-gallery {
  margin: 0 auto;
  max-width: 1030px;

		&__img {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 8px;
      max-width: 100%;
      min-height: 150px;
      overflow: hidden;

      & img {
        max-width: 100%;
        object-fit: cover;
      }
		}

		&__nav {
      position: absolute;
      top: 50%;
      left: 12.5px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: calc(100% - 50px/2);
      transform: translateY(-50%);

      @include mobile {
        left: 0;
        width: 100%;
      }
		}
    &__btn-prev, &__btn-next {
      color: var(--blue-light-color);
      cursor: pointer;
      transition: color .3s ease-in-out, opacity .3s ease-in-out;

      & svg {
        width: 25px;
        height: 40px;
        fill: currentColor;

        @include small-tablet {
          width: 18px;
          height: 30px;
        }
      }

      @include hover {
        color: var(--orange-color);
      }

      &.swiper-button-disabled {
        cursor: default;

        @include hover {
          color: var(--blue-light-color);
        }
      }
    }

}

.machine-table {

  &__wrapper {
    padding-bottom: 20px;
    max-width: 1280px;

    overflow-x: auto;

    &::-webkit-scrollbar {
      width: 8px;
      height: 8px;
    }

    &::-webkit-scrollbar-track {
      background-color: var(--light-color);
      border-radius: 2px;
    }

    &::-webkit-scrollbar-thumb {
      border-radius: 2px;
      background: var(--blue-light-color);
    }

    .simplebar-scrollbar::before {
      border-radius: 2px;
      background-color: var(--blue-light-color);
      opacity: 1;
    }
    .simplebar-track.simplebar-horizontal {
      border-radius: 2px;
    }


    & .tablepress {
      --head-text-color: #fff;
      --head-bg-color: linear-gradient(166deg,#122541 0%,#183f78 100%);
      --hover-text-color: #fff;
      --hover-bg-color: #363636;
      --odd-text-color: #363636;
      --odd-bg-color: #f5f5f5;
      --even-text-color: #363636;
      --even-bg-color: #f5f5f5;
      --border-color: #fff;
      margin: 0;
      border-style: solid;
      border-spacing: 1.5px;
      border-color: var(--border-color);
      border-collapse: separate;
      width: 100%;
    }
    & caption {
      display: none;
    }
    & thead {
      font-weight: 700;
      color: var(--head-text-color);
      background: linear-gradient(166deg, #122541 0%, #183f78 100%);
      background: var(--head-bg-color);
    }

    & tr {
      font-size: 12px;
      line-height: 1.2;

      @include mobile {
        line-height: 1;
      }
    }

    & tbody tr:nth-child(even)  {
      font-weight: 400;
      color: var(--even-text-color);
      background-color: var(--even-bg-color);
      transform: background-color .3s ease-in-out, color .3s ease-in-out;

      @include hover {
        color: var(--hover-text-color);
        background-color: var(--hover-bg-color);
      }
    }

    & tbody tr:nth-child(odd)  {
      font-weight: 400;
      color: var(--odd-text-color);
      background-color: var(--odd-bg-color);
      transform: background-color .3s ease-in-out, color .3s ease-in-out;

      @include hover {
        color: var(--hover-text-color);
        background-color: var(--hover-bg-color);
      }
    }

    & th, & td {
      padding: 14px 10px;
      text-align: center;
      font-weight: inherit;
      font-size: inherit;
      line-height: inherit;

      @include mobile {
        padding: 8px;
      }
    }

    & tbody tr td.column-1 {
      text-align: left;
    }

  }
}
