.post-hassidebar {
  &__container {
    display: flex;
    flex-shrink: 1;
    gap: 40px;
    margin: 0 auto;
    padding: 0 var(--container-offset);
    max-width: 1400px;

    @media (max-width: (922px)) {
      display: flex;
      flex-direction: column;
      gap: 40px;
    }

  }
  &__content {
    flex: 1 1 auto;
    max-width: 1120px;
    min-width: 290px;
  }
  &__aside {
    flex: 0 0 240px;

    @include tablet {
      flex: 1 0 190px;
    }
  }
}
