.sidebar {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 45px;
  padding: 16px 0;
  margin-bottom: 60px;

  @include tablet {
    align-items: center;
    gap: 30px;
    margin-bottom: 40px;
  }

  @include mobile {
    gap: 24px;
    margin-bottom: 20px;
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: calc(-39px/2);
    width: 1px;
    height: 100%;
    background-color: var(--gray-gallery-color);

    @include tablet {
      left: calc(-29px/2);
    }
  }

  &__form {
    @include small-tablet {
      width: 100%;
    }

    & .search {
      position: relative;
      top: auto;
      left: auto;
      min-width: 100%;
      opacity: 1;
      visibility: visible;
    }
    & .search__close {
      display: none;
    }

    & .search__input {
      margin-left: 0;
      border-color: transparent;
      border-radius: 10px;
      background-color: var(--gray-wild-color);

      &:-webkit-autofill,
      &:-webkit-autofill:hover,
      &:-webkit-autofill:focus {
        -webkit-box-shadow: 0 0 0px 40rem #f5f5f5 inset;
      }
    }
    & .search__input::placeholder {
      color: transparent;
    }
  }
  &__popular-news {}

  &__category {}

  &__tag-cloud {}

}
.sidebar-workers {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;

  &__item {}
}

.workers-card {
  display: flex;
  flex-direction: column;

  &__img {
    margin-bottom: 16px;
    border-radius: 8px;
    background-color: var(--gray-wild-color);
  }

  &__info {
    border-radius: 8px;
    padding: 16px 10px;
    background: linear-gradient(172deg, #469bff 0%, #153059 100%);
  }

  &__name {
    margin-top: 0;
    margin-bottom: 8px;
    font-weight: 600;
    font-size: 20px;
    line-height: 1;
    color: var(--light-color);

    @include small-tablet {
      font-size: 18px;
    }
  }

  &__position {
    margin-top: 0;
    margin-bottom: 8px;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.1;
    color: var(--light-color);

    @include small-tablet {
      font-size: 14px;
    }
  }

  &__phone {
    font-size: 16px;
    color: var(--light-color);

    @include small-tablet {
      font-size: 14px;
    }

    & svg {
      fill: currentColor;
    }
  }
}

.sidebar-email {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  padding: 16px 10px;
  width: 100%;
  font-size: 16px;
  background: linear-gradient(172deg, #469bff 0%, #153059 100%);

  @include small-tablet {
    font-size: 14px;
  }

  &__link {
    font-size: inherit;
    color: var(--light-color);

    & svg {
      fill: currentColor;
    }
  }
}

.sidebar__contacts {
  display: flex;
  flex-direction: column;
}

.contact__info {
  @include small-tablet {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.contact__email {
  @include tablet {
    font-size: 16px;
  }
}

.contact__numbers {
  display: flex;
  flex-direction: column;
  gap: 8px;
  font-size: 16px;

  @include small-tablet {
    font-size: 14px;
    align-items: center;
  }

  & .link-tel, & .link-mail {
    font-size: inherit;
  }

}

.contact__title {
  margin: 0;
  margin-bottom: 8px;
  max-width: 200px;
  font-family: var(--title-family);
  font-weight: 600;
  font-size: 20px;
  line-height: 1;
  letter-spacing: 1px;
  color: var(--dark-text-color);

  @include small-tablet {
    max-width: 100%;
    font-size: 18px;
    text-align: center;
  }
}

.sidebar-social{
  display: flex;
  flex-direction: column;
  gap: 8px;

  &__link {
    display: flex;
    gap: 8px;
    font-weight: 500;
    font-size: 16px;
    color: var(--dark-text-color);
    transition: color .3s ease-in-out;

    @include tablet {
      font-size: 14px;
    }

    @include hover {
      color: var(--orange-color);

      & img {
        animation: 2s icon-anim linear infinite;
      }
    }
  }

  & img {
    width: 20px;
    height: 20px;
    object-fit: cover;
    transform-origin: center;
    transition: transform .3s ease-in-out;
  }
  @keyframes icon-anim {
    0% {
      transform: scale(1)
    }
    50% {
      transform: scale(1.2)
    }
    100% {
      transform: scale(1)
    }

  }
}


.news-section {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;

  @include tablet {
    gap: 16px;
  }
}
.dropdown-button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 20px;
  border-radius: 10px;
  gap: 24px;
  cursor: pointer;
  text-align: left;
  transition: transform 0.3s ease, box-shadow 0.3s ease;

  @include tablet {
    padding: 8px;
  }
}

.dropdown-button:hover {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.dropdown-name {
  margin: 0;
  font-family: var(--title-family);
  font-weight: 600;
  font-size: 20px;
  line-height: 1;
  text-align: left;
  color: var(--dark-text-color);

  @include small-tablet {
    font-size: 18px;
    text-align: center;
  }
}
.dropdown-button svg {
  flex-shrink: 0;
}

.news-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 20px 16px;
  border-radius: 10px;
  background-color: var(--gray-wild-color);

  @include tablet {
    padding: 16px 10px;
  }
}

.news-item {
  font-weight: 500;
  font-size: 16px;
  line-height: 133%;
  color: var(--blue-color);
  text-decoration: none;
  transition: all 0.3s ease;

  @include tablet {
    font-size: 14px;
    text-align: center;
  }
}
.news-item:hover {
  color: var(--orange-color);
}

.popular-news {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;

  &__title {
    margin: 0;
    font-family: var(--title-family);
    font-weight: 600;
    font-size: 20px;
    line-height: 1;
    letter-spacing: 1px;
    text-align: left;
    color: var(--dark-text-color);

    @include small-tablet {
      font-size: 18px;
      text-align: center;
    }

    & span {
      font-weight: inherit;
      font-size: inherit;
      line-height: inherit;
      text-align: inherit;
      color: inherit;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 16px;
    border-radius: 10px;
    padding: 20px 16px;
    background-color: var(--gray-wild-color);

    @include small-tablet {
      padding: 16px 10px;
    }
  }

  &__item {}

  &__link {
    font-weight: 500;
    font-size: 16px;
    line-height: 1.33;
    color: var(--blue-color);
    transition: color .4s ease-in-out;

    @include hover {
      color: var(--orange-color);
    }
  }
}

.widget {
  display: flex;
  flex-direction: column;
  gap: 20px;

  @include small-tablet {
    width: 100%;
  }

  &__title {
    margin: 0;
    font-family: var(--title-family);
    font-weight: 600;
    font-size: 20px;
    line-height: 1;
    letter-spacing: 1px;
    color: var(--dark-text-color);

    @include small-tablet {
      font-size: 18px;
      text-align: center;
    }
  }

  &__list, .wp-block-categories {
    display: flex;
    flex-direction: column;
    gap: 16px;
    border-radius: 10px;
    padding: 20px 16px;
    background-color: var(--gray-wild-color);
  }

  &__item, .cat-item {
    & a {
      font-weight: 500;
      font-size: 16px;
      line-height: 1.33;
      color: var(--blue-color);
      transition: color .4s ease-in-out;

      @include hover {
        color: var(--orange-color);
      }
    }
  }
}

.wp-block-tag-cloud {
  display: flex;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 8px;
}
.tag-cloud-link {
  border-radius: 8px;
  padding: 8px 14px;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.33;
  color: var(--blue-color);
  background-color: var(--gray-wild-color);
  transition: background-color .4s ease-in-out, color .4s ease-in-out;

  @include hover {
    color: var(--light-color);
    background-color: var(--blue-color);
  }
}
